import {mapState} from 'vuex'
import axios from 'axios'
export default {
  data(){
    return {
      // posts: [],
      keyword: "",
      now_year: "2016",
      registexpos: []
    }
  },
  mounted(){
    console.log("loag")
    this.loadAll()
  },
  methods: {
    // handleEdit(row){
    //   this.$router.push("/api/registexpo")
    // },
    filterYear(value,row){
      return row.year === value;
    },

    loadAll(){
      axios.get("/api/registexpo",{
        params: {
          token: this.token,
        }
      }).then(res=>{
        this.$set(this,"registexpos",res.data)
      })
    },
  },
  computed:{
     ...mapState({
      catas: state=>state.manage.catas,
      token: state=>state.auth.token
    }),
    filteredRegistexpo(){
      return (this.registexpos || []).filter(regist=>{
        return this.keyword=="" || JSON.stringify(regist).indexOf(this.keyword)!=-1
      }).map(regist=>({
        ...regist,
        paid_record: (regist.paid_record && regist.paid_record.id)?"已填寫待確認":"未繳款",
      }))
    }
  }
}



